import Vue from 'vue'
import Router from 'vue-router'
import Activity from '@/components/Activity'
import ActivityJewelry from '@/components/ActivityJewelry'
import ActivityJoin from '@/components/ActivityJoin'
import ActivityJudges from '@/components/ActivityJudges'
import DesignerHelp from '@/components/DesignerHelp'
import JewelryUpload from '@/components/JewelryUpload'
import HelloWorld from '@/components/HelloWorld'
import ActivityToKnow from '@/components/ActivityToKnow'

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/activity',
            name: 'Activity',
            component: Activity
        },
        {
            path: '/activityjoin',
            name: 'ActivityJoin',
            component: ActivityJoin
        },
        {
            path: '/activityjewelry',
            name: 'ActivityJewelry',
            component: ActivityJewelry
        },
        {
            path: '/activityjudges',
            name: 'ActivityJudges',
            component: ActivityJudges
        },
        {
            path: '/designerhelp',
            name: 'DesignerHelp',
            component: DesignerHelp
        },
        {
            path: '/jewelryupload',
            name: 'JewelryUpload',
            component: JewelryUpload
        },
        {
            path: '/helloworld',
            name: 'HelloWorld',
            component: HelloWorld
        },
        {
            path: '/activitytoknow',
            name: 'ActivityToKnow',
            component: ActivityToKnow
        }
    ],
    scrollBehavior (to, from, savedPosition) {
        return {x: 0, y: 0}
    }
})



